<template>
  <div>
    <!-- 头部标题 -->
    <div class="header">轮播图管理</div>
    <!-- 内容区域 -->
    <div class="container">
      <!-- 搜搜区域 -->
      <div class="searchBox">
        <input
          class="searchInput"
          v-model="search.keyword"
          type="text"
          placeholder="请输入查找内容"
        />

        <button type="button" class="searchBtn" @click="searchFn">搜索</button>

        <div class="addBtn">
          <el-select v-model="swiperType" placeholder="请选择" size="mini">
            <el-option value="店铺推荐"> </el-option>
            <el-option value="前台展示"> </el-option>
          </el-select>
        </div>
      </div>
      <!-- 展示列表区 -->
      <div class="listBox">
        <!-- 轮播图列表 -->
        <div class="list">
          <div v-show="swiperList.length == 0">
            <el-empty description="没有数据"></el-empty>
          </div>
          <div
            class="swiperItem"
            v-for="swiper in swiperList"
            :key="swiper.did"
          >
            <div class="pic">
              <img class="swiperPic" :src="swiper.swiper" />
            </div>
            <div class="info">
              <ul>
                <li>店铺ID:{{ swiper.sid }}</li>
                <li>菜品ID:{{ swiper.did }}</li>
                <li>菜品名称:{{ swiper.dname }}</li>
                <li>菜品价格:{{ swiper.dprice }}</li>
              </ul>
            </div>

            <div class="setting">
              <div class="buttons">
                <!-- 编辑按钮 -->
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  title="通过推荐"
                  v-show="swiper.swiperlevel == 1"
                  @click="agreeFn(swiper.did)"
                ></el-button>
                <!-- 删除按钮 -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  title="拒绝推荐"
                  @click="disAgreeFn(swiper.did)"
                ></el-button>
              </div>
            </div>
          </div>
        </div>
        <!-- 分页器 -->
        <div class="pager">
          <!-- 分页区域 -->
          <el-pagination
            @current-change="handleCurrentChangeFn"
            :current-page.sync="search.pagenum"
            :page-size.sync="search.pagesize"
            layout="total, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getShopSwiperListAPI,
  getSwiperListAPI,
  agreeSugAPI,
  disAgreeSugAPI,
} from "@/api";
export default {
  name: "swiperCMD",
  // ==========================================以下是data==========================================
  data() {
    return {
      search: {
        pagenum: 1,
        pagesize: 2,
        keyword: "",
      },
      swiperType: "店铺推荐",
      // 总共的轮播图数
      total: 0,
      swiperList: [],

      // 用于控制dialogform
      dialogFormVisible: false,
      // 用于控制表单label的宽
      formLabelWidth: "120px",
    };
  },
  // ==========================================以下是data==========================================
  methods: {
    // 通过推荐
    async agreeFn(foodID) {
      const { data: res } = await agreeSugAPI({ id: foodID });
      if (res.code != 0) return this.$message.error(res.message);
      this.$message.success("推荐成功！");
      this.afterSug();
    },
    async disAgreeFn(foodID) {
      const { data: res } = await disAgreeSugAPI({ id: foodID });
      if (res.code != 0) return this.$message.error("拒绝失败！");
      this.$message.success("拒绝成功！");
      this.afterSug();
    },
    afterSug() {
      this.search.pagenum = 1;
      if (this.swiperType == "店铺推荐") {
        this.getShopSwiperList();
      } else {
        this.getSwiperList();
      }
    },
    // 获取店铺推荐的轮播图
    async getShopSwiperList() {
      const { data: res } = await getShopSwiperListAPI({
        page: this.search.pagenum,
        dname: this.search.keyword,
      });
      console.log(res);
      if (res.code != 0) return this.$message.error("获取轮播图列表失败！");
      this.swiperList = res.data;
      this.total = res.count;
    },
    // 获取前台展示的轮播图列表
    async getSwiperList() {
      const { data: res } = await getSwiperListAPI({
        page: this.search.pagenum,
        dname: this.search.keyword,
        pagesize: this.search.pagesize,
      });
      if (res.code != 0 && res.code != 1)
        return this.$message.error("获取轮播图列表失败！");
      if (res.code == 1) {
        this.swiperList = [];
        this.total = 0;
      } else {
        this.swiperList = res.data;
        this.total = res.count;
      }
    },
    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      if (this.swiperType == "店铺推荐") return this.getShopSwiperList();
      return this.getSwiperList();
    },
    // 搜索按钮
    searchFn() {
      if (this.search.keyword == "") {
        this.$message("请输入搜索关键词！");
      }
      this.search.pagenum = 1;
      this.getShopSwiperList();
    },
  },
  // ==========================================以下是mounted==========================================
  mounted() {
    this.getShopSwiperList();
  },
  // ==========================================以下是监视器==========================================

  watch: {
    swiperType: {
      immediate: true,
      handler(newV) {
        this.search.pagenum = 1;
        this.search.keyword = "";
        if (newV == "店铺推荐") {
          this.getShopSwiperList();
        } else {
          this.getSwiperList();
        }
      },
    },
  },
};
</script>

<style scoped>
.header {
  height: 60px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  line-height: 60px;
  font-size: 40px;
  font-family: "幼圆";
  font-weight: 600;
  color: rgb(84, 87, 87);
  background-color: rgb(197, 199, 205);
  border: rgb(183, 184, 187) solid 10px;
  border-radius: 10px;
}
.container {
  width: 90%;
  height: 485px;
  background-color: rgb(186, 193, 193);
  margin: 10px auto;
  border-radius: 10px;
  overflow: hidden;
}
.searchBox {
  width: 65%;
  height: 40px;
  margin: 10px auto;
  vertical-align: middle;
  overflow: hidden;
}
.searchInput {
  width: 75%;
  height: 30px;
  font-size: 15px;
  border-radius: 6px;
  border: rgb(242, 240, 210) solid 1px;
}
.searchBtn {
  width: 60px;
  height: 30px;
  font-size: 15px;
  color: rgb(67, 70, 72);
  background-color: rgb(169, 193, 225);
  border: rgb(127, 171, 228) solid 2px;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 10px;
}
.searchBtn:active {
  background-color: rgb(248, 248, 248);
  border: rgb(232, 233, 235) solid 2px;
}
.addBtn {
  float: right;
  width: 100px;
  margin-left: 10px;
}

.listBox {
  width: 80%;
  height: 400px;
  background-color: rgb(244, 244, 245);
  border-radius: 5px;
  margin: auto;
  overflow: hidden;
}
.list {
  width: 95%;
  height: 350px;
  margin: 8px auto;
  overflow: hidden;
}
.swiperItem {
  width: 98%;
  height: 160px;
  background-color: rgb(202, 204, 207);
  margin: 8px auto;
  border-radius: 8px;
  overflow: hidden;
}
.swiperItem:hover {
  box-shadow: 0 2px 12px 0 rgb(109, 102, 102);
}
.pic {
  width: 320px;
  height: 140px;
  background-color: rgb(232, 227, 221);
  float: left;
  margin-left: 10px;
  margin-top: 10px;
}
.swiperPic {
  width: 320px;
  height: 140px;
}
.info {
  float: left;
  margin-left: 10px;
  margin-top: 10px;
  height: 140px;
  width: 300px;
  background-color: rgb(232, 227, 221);
}
.info ul li {
  list-style: none;
  width: 200px;
  font-size: 16px;
  line-height: 25px;
  margin-top: 2px;
  color: rgb(84, 80, 75);
  font-family: "楷体";
  font-weight: 600;
  border-bottom: black solid 2px;
}
.setting {
  float: left;
  margin-left: 10px;
  margin-top: 10px;
  height: 140px;
  width: 190px;
  background-color: rgb(232, 227, 221);
}
.buttons {
  width: 145px;
  height: 40px;
  margin-top: 50px;
  margin-left: 35px;
}
.pager {
  width: 65%;
  height: 30px;
  margin: auto;
}
.the_img {
  width: 320px;
  height: 140px;
}
</style>
